import 'whatwg-fetch'
import 'es6-promise'

export var get = function(path) {
  return new Promise((resolve, reject) => {
      fetch(path,{
        method: "GET",
        //credentials: 'include',
        mode: "cors",//跨域请求
        headers:
        {
          'Accept':'application/json,text/plain,*/*',
        }
      }).then(res => { 
        return res.json();
      })
      .then(json => {
          resolve(json);
      })
      .catch(err => {
          reject(err);
      });
  });
}