import 'whatwg-fetch'
import 'es6-promise'

export function post(url,val) {
  return new Promise((resolve, reject) => {
        fetch(url, {
        method: 'POST',
        mode:"cors",
        headers: {  
          "Content-Type": "application/json;charset=UTF-8",
          
        },
        body: JSON.stringify(val),
        
        })
        .then((res) => {
          return res.json()
        }).then((response) => {
          resolve(response);
        })
        .catch((error) => {
          reject({status:-1});
        });
      })
    }
