		import React, { useState, useEffect }from 'react';
		import ReactDOM from 'react-dom';
		import "./App.css"
		import { Form, Input, Button, DatePicker,notification } from 'antd';
		import 'antd/dist/antd.css';
		import{get} from './fetch/get.js'
		import{post} from './fetch/post.js'
		import moment from 'moment';


		const layout = {
				labelCol: {
					span: 7,
				},
				wrapperCol: {
					span: 11,
				},
			};
		const tailLayout = {
				wrapperCol: {
					offset: 0,
					span: 16,
				},
			};

		class About extends React.Component{

				render(){
						const onFinish = values => {
								console.log('Success:', values);
								post("http://10.109.66.93:3000/create",values);
							};
							
							const onFinishFailed = errorInfo => {
								console.log('Failed:', errorInfo);
							};
							const openNotification = () => {
								notification.open({
									message: 'Notification Title',
									description:
										'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
									onClick: () => {
										console.log('Notification Clicked!');
									},
								});
							};
						return(
								<div>
										
										<div id="dashboard" >
										<text style={{fontSize:24}}>Dashboard</text>
										</div>
										<div id="good-re">
										<div >
										<Form
												{...layout}
												name="item"
												style={{paddingTop:50}}
												initialValues={{
														remember: true,
												}}
												onFinish={onFinish}
												onFinishFailed={onFinishFailed}
										>
												<Form.Item
														label="料号："
														name="item_id"
													
														rules={[
														{
														required: true,
														message: '请输入料号!',
														},
														]}
												>
														<Input placeholder="Item Number"/>
												</Form.Item>

												<Form.Item
														label="数量："
														name="item_number"
														rules={[
														{
																required: true,
																message: '请输入数量!',
														},
														]}
												>
														<Input placeholder="Quantity"/>
												</Form.Item>
												<Form.Item
														label="收料日期："
														name="receiving_date"
														rules={[
														{
																required: true,
																message: '请输入收料日期!',
														},
														]}
												>
														<DatePicker style={{ width: '100%' }} placeholder="Warehousing Date"/>
												</Form.Item>
												<Form.Item
														label="生产日期："
														name="production_date"
														rules={[
														{
																required: true,
																message: '请输入生产日期!',
														},
														]}
												>
														<DatePicker style={{ width: '100%' }} format="YYYY-MM-DD"/>
												</Form.Item>
												<Form.Item
														label="厂商："
														name="manufacturer"
														rules={[
														{
																required: true,
																message: '请输入厂商!',
														},
														]}
												>
														<Input placeholder="Manufacturer"/>
												</Form.Item>
												<Form.Item {...tailLayout}>
														<div align="center">
														<Button type="primary" htmlType="submit" >
													  	Submit
														</Button>
														</div>
												</Form.Item>
										</Form>
										</div>
						

										</div>

		





										<div style={{height:100}}>
									</div>

								</div>
						)
				}
		}
		export default About