import React, { useState, useEffect }from 'react';
import ReactDOM from 'react-dom';
import "./App.css"
import { Input, Row, Col ,Button,Card,Typography,message,Modal} from 'antd';
import { AudioOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import { get } from './fetch/get';
import{post} from './fetch/post.js'


const {Text} = Typography;
const info = () => {
  message.info('Input box should not be empty');
};
const finderrer = () => {
  message.info("Can't find in database");
};
const inputerrer = () => {
  message.info("Input number greater than inventory");
};
const inputNumbererrer = () => {
  message.info("Please enter number");
};
message.config({
  maxCount: 3,
});
export default class Send extends React.Component{
  
  constructor(){
    super();
    this.state={
      item_id:"",
      item_number:"",
      receiving_date:"",
      production_date:"",
      manufacturer:"",
      visible: false ,
      receive_number:"",
      input_value:"",
    }
    }
    showModal = () => {
      this.setState({
        visible: true,
      });
    };
  
    handleOk = e => {
      console.log(e);
      this.setState({
        visible: false,
      });

      var reduceValue={item_id:this.state.item_id,reduce_number:this.state.receive_number};
      console.log("reduceValue:",reduceValue);
      post('http://10.109.66.93:8000/reduce',reduceValue).then(
          data=>{
            var value=JSON.parse(data);
            if(value.item_id!=="404"){
              this.setState({
               item_id:value.item_id,
               item_number:value.item_number,
               receiving_date:value.receiving_date,
               production_date:value.production_date,
               manufacturer:value.manufacturer,
               });
            }
            else{
              finderrer();
            }
            console.log("search res data",value);
          }
      )

    };
  
    handleCancel = e => {
      console.log(e);
      this.setState({
        visible: false,
      });
    };
   render(){
  
   const { Search } = Input;
    const suffix = (
      <AudioOutlined
        style={{
          fontSize: 16,
          color: '#1890ff',
        }}
      />
    );
    const update=e=>{
      const val=e.target.value;
      this.setState({input_value:val});
      const pcs=Number(this.state.item_number) ;//
      const re=new RegExp("^[0-9]*$");//正则只能输入数字
      if(val.match(re)&&val.length>0){
        if(pcs!==null&&val<=pcs){
          this.setState({receive_number:val});
          console.log(this.state.receive_number);
        }
        else{
          inputerrer();
        }
      }
      else{
        inputNumbererrer();
      }
      
    }
    const updatedatabase=()=>{
      console.log("renu",this.state.receive_number);
      console.log("innu",this.state.input_value);
      if(this.state.receive_number===this.state.input_value&&this.state.receive_number.length>0&&this.state.input_value.length>0)
      {
        this.showModal();
      }
      else{
        inputNumbererrer();
      }
    }

    const search=value=>{
        console.log("search input data",value);
        if(value.length>0){
          var searchvalue={item_id:value}
          post('http://10.109.66.93:8000/search',searchvalue).then(
          data=>{
            var value=JSON.parse(data);
            if(value.item_id!=="404"){
              this.setState({
               item_id:value.item_id,
               item_number:value.item_number,
               receiving_date:value.receiving_date,
               production_date:value.production_date,
               manufacturer:value.manufacturer,
               });
            }
            else{
              finderrer();
            }
            console.log("search res data",value);
          });
        }else{
          info();
          console.log("search input data","null");
        }     
    }
            return(
              <div>
                <div id="dashboard" >
                  <text style={{fontSize:24}}>Dashboard</text>
                </div>
                <div>
                </div>
                <div id="good-se">
                <Search
                  placeholder="input search text"
                  enterButton="Search"
                  size="large"
                  suffix={suffix}
                  style={{width:360}}
                  onSearch={search}
                  />
                </div>
                <br/>
                <div id="good-re" >
                <Card title="ITEM INFO" bordered={false} extra={<Input placeholder="领取数量" onChange={update} bordered={false}/>}>
                <br/>
                  <Row>
                    
                    <Col span={4}><strong>ITEM_ID:</strong></Col>
                    <Col span={8}><div id="test">{this.state.item_id}</div></Col>
                   
                    <Col span={4}><strong>ITEM_NUMBER(Pcs):</strong></Col>
                    <Col span={8}><div>{this.state.item_number}</div></Col>
                  </Row>
              
                  <br/>
                  <Row> 
                    <Col span={4}><strong>MANUFACTURER:</strong></Col>
                    <Col span={4}><div>{this.state.manufacturer}</div></Col>
                  </Row>
                  <br/> 
                  <Row>
                    
                    <Col span={4}><strong>RECEIVE_DATA:</strong></Col>
                    <Col span={8}><div>{this.state.receiving_date}</div></Col>
                  </Row>
                  <br/>
                  <Row>
                    <Col span={4}><strong>PRODUCTION_DATA:</strong></Col>
                    <Col span={8}><div>{this.state.production_date}</div></Col>
                  </Row>
                </Card>
                 <br/>
                 <div align="center">
                 <Button type="primary" onClick={updatedatabase}>Confirm</Button>
                 </div>
                 <div>
                 <Modal
                   title="确认领取"
                   visible={this.state.visible}
                   onOk={this.handleOk}
                   onCancel={this.handleCancel}
                 >
                   <p><strong>item_id:</strong> {this.state.item_id}</p>
                   <br/>
                  <p><strong>领取数量: </strong> {this.state.receive_number}</p>
                  </Modal>
                 </div>
                </div>
                <div style={{height:100}}>
                </div>
              </div>
            )
          }
        }