
import React, { useState, useEffect }from 'react';
import ReactDOM from 'react-dom';
import { DualLine ,ColumnLine} from '@ant-design/charts';
import 'antd/dist/antd.css';
import './App.css';
import { Menu,Input,Row,Col,Statistic,Button,Table, Tag, Space } from 'antd';
import { ArrowUpOutlined,NumberOutlined,HistoryOutlined,PieChartOutlined,StarOutlined,PlusSquareOutlined,MinusSquareOutlined } from '@ant-design/icons';
import{get} from './fetch/get.js';
import{post} from './fetch/post.js';
import{getdata} from './fetchJsonp.js';
import { formatTimeStr } from 'antd/lib/statistic/utils';
var mailer=require("./mail")

const { SubMenu } = Menu;
const logoSvg = require("./logo.svg")
const { Search } = Input;
const { Column, ColumnGroup } = Table;
const mail=require("./sendMail")


const uvData = [
  {
    time: '2019-03',
    value: 350,
  },
  {
    time: '2019-04',
    value: 900,
  },
  {
    time: '2019-05',
    value: 300,
  },
  {
    time: '2019-06',
    value: 450,
  },
  {
    time: '2019-07',
    value: 470,
  },
];
const transformData = [
  {
    time: '2019-03',
    count: 800,
  },
  {
    time: '2019-04',
    count: 600,
  },
  {
    time: '2019-05',
    count: 400,
  },
  {
    time: '2019-06',
    count: 380,
  },
  {
    time: '2019-07',
    count: 220,
  },
];
const config = {
  
  description: {
    visible: true,
    text: '柱线混合图表',
  },
  data: [uvData, transformData],
  xField: 'time',
  yField: ['value', 'count'],
};

class Home extends React.Component{
  constructor(){
    super();
    this.state={
    tableData:[],
    total:"",
    overdue:"",
    percentage:"",
  }
  }
  componentDidMount(){
    //在渲染前调用
    get("http://10.109.66.93:8000/overdue").then(
      value=>{ 
        var data=value;
        this.setState({
          tableData:data
        });
        this.setState({
          total:data[data.length-1].total
        });
        this.setState({
          overdue:data[data.length-1].overdue_number
        });
        this.setState({
          percentage:(this.state.overdue/this.state.total)*100
        });
        console.log("Table返回数据",this.state.tableData);
      }
     ); 
    console.log(global.data)
  }
    render(){
    
     const onClick = () => {
        console.log("ddw");
        //mailer.sendMail("jack.lin@legrand.com.cn","test","<p>你好,jack</p>")
        //mail.scheduleCronstyle();
        console.log("shuju");
      };   
        return(
          <div>
          <div id="dashboard" >
          <text style={{fontSize:24}}>Dashboard</text>
          </div>
          <div id="val">
            <Row>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="TOTAL ITEM"
                   value={this.state.total}
                   
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<NumberOutlined />}
                  
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="OVERDUE DATA"
                   value={this.state.overdue}
                   
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<HistoryOutlined />}
                  
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="PERCENTAGE OVERDUE"
                   value={this.state.percentage}
                   precision={2}
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<PieChartOutlined />}
                   suffix="%"
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="TOTAL NUMBER"
                   value={150}
  
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<StarOutlined />}
                  
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="NEWLY INCREASED"
                   value={10}
                  
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<PlusSquareOutlined/>}
                  
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
              <Col span={4}>
                <div className="valBox">
                  <Statistic
                   title="REDUCE"
                   value={2}
             
                   valueStyle={{ color: '#2775B6' }}
                   prefix={<MinusSquareOutlined  />}
                  
                   style={{paddingTop:5}}
                  />
                </div>
                
              </Col>
            </Row>

          </div>
          <br/>
       
        <div id="first">
          <div id="table">
            <Table dataSource={this.state.tableData} pagination={false} backgroundColor="#ffffff" style={{height:280}}>
              <Column title="ITEM_ID" dataIndex="item_id" key="item_id" className="table" />
              <Column title="ITEM_NUMBER" dataIndex="item_number" key="item_number" className="table" />
              <Column
                title="MANUFACTERY"
                dataIndex="manufacturer"
                key="manufacturer"
                className="table" />
              <Column
                key="production_date"
                title="PRODUCT_DATA"
                dataIndex="production_date"
                className="table" 
              />
              <Column
                key="receiving_date"
                title="RECEIVE_DATA"
                dataIndex="receiving_date"
                className="table" 
              />
            </Table> 
          </div>

           <div id="lineChart"> 
           <ColumnLine {...config} style={{width:480,height:350,backgroundColor:"#ffffff"}}/>
          
          </div>
                  
          
      
        </div>
        <Button type="primary" onClick={onClick}>Primary Button</Button>
        </div>
        )
    }
}
export default Home